import { f as fail } from "../../../chunks/index2.js";
const actions = {
  headersCheck: async ({ request, fetch }) => {
    try {
      const data = await request.formData();
      let url = data.get("url")?.toString() || "";
      if (!url.startsWith("http://") && !url.startsWith("https://")) {
        url = "https://" + url;
      }
      console.log(`[Server Action] Checking headers for URL: ${url}`);
      if (!url) {
        return fail(400, { error: "URL is required" });
      }
      try {
        const controller = new AbortController();
        const timeoutId = setTimeout(() => controller.abort(), 1e4);
        const response = await fetch(url, {
          method: "HEAD",
          headers: {
            "User-Agent": "Route-Network-Tools/1.0"
          },
          redirect: "manual",
          // Don't follow redirects automatically
          signal: controller.signal
        });
        clearTimeout(timeoutId);
        const headers = {};
        response.headers.forEach((value, key) => {
          headers[key] = value;
        });
        const securityHeaders = {
          "Content-Security-Policy": headers["content-security-policy"],
          "Strict-Transport-Security": headers["strict-transport-security"],
          "X-Content-Type-Options": headers["x-content-type-options"],
          "X-Frame-Options": headers["x-frame-options"],
          "X-XSS-Protection": headers["x-xss-protection"],
          "Referrer-Policy": headers["referrer-policy"]
        };
        const cacheHeaders = {
          "Cache-Control": headers["cache-control"],
          "Expires": headers["expires"],
          "Last-Modified": headers["last-modified"],
          "ETag": headers["etag"]
        };
        const corsHeaders = {
          "Access-Control-Allow-Origin": headers["access-control-allow-origin"],
          "Access-Control-Allow-Methods": headers["access-control-allow-methods"],
          "Access-Control-Allow-Headers": headers["access-control-allow-headers"],
          "Access-Control-Max-Age": headers["access-control-max-age"]
        };
        const serverHeaders = {
          "Server": headers["server"],
          "X-Powered-By": headers["x-powered-by"],
          "Via": headers["via"]
        };
        const contentHeaders = {
          "Content-Type": headers["content-type"],
          "Content-Length": headers["content-length"],
          "Content-Encoding": headers["content-encoding"],
          "Content-Language": headers["content-language"]
        };
        const securityScore = calculateSecurityScore(headers);
        return {
          url,
          statusCode: response.status,
          statusText: response.statusText,
          allHeaders: headers,
          categorizedHeaders: {
            security: securityHeaders,
            cache: cacheHeaders,
            cors: corsHeaders,
            server: serverHeaders,
            content: contentHeaders
          },
          securityScore
        };
      } catch (error) {
        console.error("[Server Action] Fetch error:", error);
        return fail(500, {
          error: error.name === "AbortError" ? "Request timed out" : `Failed to fetch headers: ${error.message}`
        });
      }
    } catch (e) {
      console.error("[Server Action] Headers check error:", e);
      return fail(500, { error: e.message || "Failed to check headers" });
    }
  }
};
function calculateSecurityScore(headers) {
  let score = 0;
  const maxScore = 100;
  if (headers["content-security-policy"]) {
    score += 20;
  }
  if (headers["strict-transport-security"]) {
    score += 20;
  }
  if (headers["x-content-type-options"]?.includes("nosniff")) {
    score += 15;
  }
  if (headers["x-frame-options"]) {
    score += 15;
  }
  if (headers["x-xss-protection"]) {
    score += 15;
  }
  if (headers["referrer-policy"]) {
    score += 15;
  }
  let category = "";
  if (score >= 80) {
    category = "excellent";
  } else if (score >= 60) {
    category = "good";
  } else if (score >= 40) {
    category = "fair";
  } else {
    category = "poor";
  }
  return {
    score,
    max: maxScore,
    category
  };
}
export {
  actions
};
